<!--往期精彩活动页-->
<!--created by qiuwei-->
<template>
  <div class="page-history">
    <div class="part-list">
      <goodList :data="list" v-jd-page:config.bindWindow="loadData" :allLoad="allLoad"/>
    </div>
    <div class="part-end" v-if="allLoad.is && list.length > 0"><span>已经到底啦</span></div>
    <div class="part-none" v-if="allLoad.is && list.length === 0">还没有往期活动哦～</div>
  </div>
</template>

<script>
import { pageWxShare } from '../../assets/js/util'
import { mapState, mapActions } from 'vuex'
import goodList from '../home/goods-list'

export default {
  name: 'page-history',
  components: { goodList },
  computed: {
    ...mapState('history', [
      'list',
      'allLoad'
    ])
  },
  created () {
    document.body.style.background = '#f7f7f7'
    document.body.scrollTop = 0
  },
  mounted () {
    // 微信分享图文控制
    pageWxShare()
  },
  methods: {
    ...mapActions('history', [
      'loadData'
    ])
  }
}
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
  .page-history {
    background: #f7f7f7;
    .part-list {
      padding: 16px 24px;
      .goods-panel {
        box-shadow: 0 12px 30px 0 #f1f1f1;
      }
    }
    .part-end {
      text-align: center;
      padding: 24px 0 40px;
      font-size: 24px;
      color: #ccc;
      span {
        position: relative;
        &:before, &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: -65px;
          height: 1PX;
          width: 50px;
          background: linear-gradient(to right, transparent 0%, #ccc 100%);
        }
        &:after {
          left: auto;
          right: -65px;
          background: linear-gradient(to left, transparent 0%, #ccc 100%);
        }
      }
    }
    .part-none {
      color: #333;
      font-size: 24px;
      text-align: center;
      padding: 20px 0;
    }
  }
</style>



