<!--商品列表-->
<!--created by qiuwei-->
<template>
  <ul class="page-goods-list">
    <li class="goods-panel" :class="{'goods-panel--join': item.participated}" v-for="item in dealData" :key="item.id" @click="goDetail(item)">
      <div class="goods-head">
        <div class="head-img">
          <img v-lazy="{src: $supImg(item.starHeadImg, '90x90'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
        </div>
        <div class="head-info">
          <span class="info-name">{{item.starName || ''}}</span>
          <span class="info-line" v-if="item.starName && item.starLabel">|</span>
          <span class="info-work">{{item.starLabel || ''}}</span>
          <p
            class="info-heart"
            :class="{
              'info-heart--active': item.periodStatus === 903,
              'info-heart--end': [902, 903].indexOf(item.periodStatus) === -1
            }"
          >
            <i></i>
            <jd-integer :value="item.praiseTotalCount || 0"/>
          </p>
        </div>
        <div class="head-time" v-if="[902, 903].indexOf(item.periodStatus) !== -1">
          <span>{{item.periodStatus === 902 ? '距开始' : '距结束'}}</span>
          <jd-timer :data="item.periodStatus === 902 ? (item.startTime || 0) : (item.endTime || 0)" @endBack="endBack(item)"/>
        </div>
        <div class="head-over" v-if="[902, 903].indexOf(item.periodStatus) === -1">
          已结束
        </div>
      </div>
      <p class="goods-title">
        {{item.goodsName || ''}}
      </p>
      <ul class="goods-img">
        <li v-for="item1 in item.detailImgs" :key="item1">
          <div>
            <img v-lazy="{src: $supImg(item1, '214x160')}"/>
          </div>
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'page-goods-list',
  props: {
    data: {
      type: Array,
      default: () => []
    } // 列表数据
  },
  computed: {
    dealData () {
      // 图片长度最多取3
      this.data.forEach(e => {
        if (e.detailImgs && e.detailImgs.length > 3) {
          e.detailImgs.length = 3
        }
      })
      return this.data
    }
  },
  methods: {
    // 跳详情页
    goDetail (item) {
      this.$router.push(`/detail?periodCode=` + item.periodCode)
    },
    // 倒计时结束
    endBack (item) {
      item.periodStatus++
    }
  }
}
</script>

<style lang="scss">
  .page-goods-list {
    .goods-panel {
      position: relative;
      padding: 32px 24px 24px;
      background: #fff;
      box-shadow: 0 12px 30px 0 rgba(255, 52, 52, .05);
      border-radius: 4px;
      &--join {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          width: 100px;
          height: 80px;
          background: url(../../assets/img/join@3x.png) no-repeat center;
          background-size: auto 100%;
        }
      }
    }
    .goods-panel + .goods-panel {
      margin-top: 16px;
    }
    .goods-head {
      position: relative;
      z-index: 1;
      .head-img {
        display: inline-block;
        vertical-align: middle;
        width: 88px;
        height: 88px;
        background: #efefef;
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      .head-info {
        display: inline-block;
        vertical-align: middle;
        padding: 8px 0 0 16px;
        .info-name {
          display: inline-block;
          height: 40px;
          line-height: 44px;
          font-size: 28px;
          font-weight: bold;
          color: #333;
          vertical-align: baseline;
        }
        .info-line {
          color: #d9d9d9;
          font-size: 16px;
          vertical-align: 4px;
          margin: 0 5px;
        }
        .info-work {
          font-size: 22px;
          color: #999;
          vertical-align: baseline;
        }
        .info-heart {
          position: relative;
          top: -3px;
          height: 40px;
          line-height: 40px;
          font-size: 24px;
          color: #ccc;
          .jd-integer {
            position: relative;
            left: -10px;
            top: -1px;
          }
          & > i {
            display: inline-block;
            width: 40px;
            height: 40px;
            background: url(../../assets/img/heart@3x.jpg) no-repeat center;
            background-size: auto 100%;
            filter: grayscale(1);
            opacity: .3;
          }
          /*进行中*/
          &.info-heart--active {
            color:  #ff3434;
            & > i {
              background: url(../../assets/img/heart.gif) no-repeat center;
              background-size: auto 100%;
              filter: none;
              opacity: 1;
            }
          }
          /*结束*/
          &.info-heart--end {
            color:  #ff3434;
            & > i {
              filter: none;
              opacity: 1;
            }
          }
        }
      }
      .head-time {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        font-size: 24px;
        color: #000;
        & > span {
          vertical-align: middle;
        }
        .jd-timer {
          white-space: nowrap;
          margin-left: 5px;
          vertical-align: middle;
        }
      }
      .head-over {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        font-size: 24px;
        font-weight: 300;
        color: #fff;
        background: #999;
        border-radius: 25px;
        padding: 5px 15px;
      }
    }
    .goods-title {
      margin-top: 24px;
      font-size: 28px;
      color: #333;
      max-height: 999999px;
      font-weight: 300;
    }
    .goods-img {
      font-size: 0;
      margin-top: 24px;
      & > li {
        display: inline-block;
        width: 33.33%;
        height: 160px;
        div {
          height: 100%;
          background: #efefef;
          border-radius: 5px;
          img {
            width: 100%;
            height: 100%;
            border-radius: 4px;
          }
        }
      }
      & > li:nth-child(-n+2) {
        padding-right: 6px;
      }
    }
  }
  /*安卓下一些特殊处理*/
  .underAndroid {
    .page-goods-list {
      .jd-timer-item {
        padding-top: 10px;
      }
    }
  }
</style>



